* {
  margin: 0;
  padding: 0;
}
.center {
  margin: 0 auto;
  width: 1280px;
  position: relative;
}
ul,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  color: inherit;
  text-decoration: none;
}
button {
  background-color: #f2b3dc;
  color: #40182b;
  border: none;
  font-size: 0.8rem;
  padding: 2px 5px;
  box-sizing: border-box;
  border-radius: 10px;
}
.App {
  position: relative;
}
.App > * {
  /* z-index: 9; */
}
.App .star-box {
  z-index: -1;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
}
footer {
  position: relative;
  width: 100vw;
  border-top: 1px solid #f2b3dc;
  background-color: #fff;
  height: 100px;
}
footer .center {
  height: 100px;
}
footer span {
  position: absolute;
  color: #40182b;
  font-size: 0.8rem;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}
